.section-1 {
  .img-cont {
    width: 100%;
    overflow: hidden;

    > img {
      width: 100%;
    }
  }
}

.section-2 {
  margin-top: 10rem;

  > .title {
    font-size: 2.5rem;
    color: #fff;
    margin-bottom: 1rem;
    font-weight: 500;
    text-align: center;
  }

  > .sub-title {
    font-size: 1rem;
    color: #fff;
    margin-bottom: 3rem;
    font-weight: 300;
    text-align: center;
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 5rem;

    .item {
      width: calc(50% - 2rem);

      .img-cont {
        width: 100%;
        overflow: hidden;
        border-radius: 30px;

        img {
          width: 100%;
        }
      }

      .sub {
        font-size: 1.2rem;
        color: #fff;
        margin-top: 1rem;
      }

      .desc {
        list-style: disc;
        font-size: 1rem;
        color: #fff;
        margin-top: 1rem;
        padding-left: 1rem;
      }
    }
  }
}

.section-3 {
  margin-top: 10rem;

  > .title {
    font-size: 2.5rem;
    color: #fff;
    margin-bottom: 3rem;
    font-weight: 500;
    text-align: center;
  }

  .img-cont {
    border-radius: 30px;
  }
}

.section-4 {
  margin-top: 10rem;

  > .title {
    font-size: 2.5rem;
    color: #fff;
    margin-bottom: 3rem;
    font-weight: 500;
    text-align: center;
  }

  .sub-title {
    font-size: 1.2rem;
    color: #fff;
  }

  .desc {
    list-style: disc;
    font-size: 1rem;
    color: #fff;
    padding-left: 1rem;
    margin-top: 1rem;
  }
}

@media (max-width: 768px) {
  .main-inner {
    .page-title {
      .title {
        display: inline-block;
      }
    }
  }

  .section-1 {
    padding-top: 5rem;
  }

  .section-2 {
    > .title {
      font-size: 2.8rem;
    }

    .sub-title {
      font-size: 1.5rem;
    }

    .list {
      .item {
        width: 100%;

        .sub {
          font-size: 2rem;
        }

        .desc {
          font-size: 1.5rem;
          margin-left: 1rem;

          > li {
            margin-bottom: 0.5rem;
          }
        }
      }
    }
  }

  .section-4 {
    .sub-title {
      font-size: 2rem;
    }
    .desc {
      font-size: 1.5rem;
      margin-left: 1rem;

      > li {
        margin-bottom: 0.5rem;
      }
    }
  }
}
